.skills-container {
  overflow: hidden;
  white-space: nowrap;
  width: 100%;
  min-height: 100vh;
  padding: 10px;
  margin-top: 60px;
  text-align: center;
  position: relative;
}

.skills-marquee {
  display: inline-block;
  animation: scroll 15s linear infinite;
}

.skills-marquee.paused {
  animation-play-state: paused;
}

@keyframes scroll {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}

.skills-category .skills-card {
  display: inline-block;
  margin: 10px;
  width: 100px;
  height: 120px;
  padding: 10px;
  background-color: #fff;
  border: 1px solid #000;
  box-shadow: 0px 10px 30px rgba(39, 61, 139, 0.2); 
  border-radius: 8px;
  text-align: center;
  overflow: hidden;
  vertical-align: top;
  transition: 0.5s all;
}

.skills-card img {
  width: 80%;
  height: 60%;
  display: block;
  margin: 0 auto;
}

.skills-card .skills-card-text {
  font-size: 15px;
  color: black;
  text-align: center;
  white-space: normal;
  margin-top: 10px;
}

.skills-card:hover {
  box-shadow: 10px 20px 26px rgb(11, 32, 70);
}
